import React, { Component, Fragment } from "react";
import { IoFilter } from "react-icons/io5";
import {
  MdCheckBox,
  MdCheckBoxOutlineBlank,
  MdLocationOn,
  MdSearch,
} from "react-icons/md";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { Auth, PushPopupAlert } from "../../../actions";
import searchData from "../../../utils/search";
import { getAllRoom } from "../../../actions/location";
import { getDistrictPlacement, getSchools } from "../../../actions/schools";
import { getPositions } from "../../../actions/settings";
import { PopupAlertData } from "../../../components/Alert/Popup";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { PositionAPI } from "../../../interfaces";
import {
  placedCandidateInterface,
  PlacedCandidateStatus,
  placementReportContentInterface,
  placementReportContentKeyInterface,
  PlacementToPrintInterface,
} from "../../../interfaces/marks_publication";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import {
  capitalizeFirstLetter,
  isEmptyOrSpaces,
} from "../../../shared/dataTest";
import {
  DistrictInterface,
  RoomLocationInterface,
  SchoolsInterface,
} from "../../../shared/interfaces";
import {
  generatePDF,
  reportSettingsInterface,
} from "../../../components/DownloadPDF/rest";
import { BiExport, BiSort } from "react-icons/bi";
import {
  MainItem,
  SideNavigation,
} from "../../../components/Navigation/SideNavigation";
import { FiEdit } from "react-icons/fi";
import TableLoading from "../../../components/Loading/TableLoading";
import { ImFilesEmpty } from "react-icons/im";
import { search, sortThisArray } from "../../../utils/functions";
import Loading from "../../../components/Loading/Loading";
import ExportToExcel from "../../../utils/ExportToExcel";

const defaultReportContent: placementReportContentInterface[] = [
  {
    key: placementReportContentKeyInterface.applicant_name,
    title: "Applicant name",
    status: true,
  },
  {
    key: placementReportContentKeyInterface.national_id,
    title: "National ID",
    status: true,
  },
  {
    key: placementReportContentKeyInterface.email,
    title: "Email",
    status: true,
  },
  {
    key: placementReportContentKeyInterface.phone_number,
    title: "Phone",
    status: true,
  },
  {
    key: placementReportContentKeyInterface.position_name,
    title: "Position",
    status: true,
  },
  {
    key: placementReportContentKeyInterface.school_name,
    title: "School name",
    status: true,
  },
  {
    key: placementReportContentKeyInterface.status,
    title: "Status",
    status: true,
  },
];

interface PlacementReportProps {
  //   REDUX ############################
  auth: Auth;
  schools: SchoolsInterface[];
  districtPlacements: placedCandidateInterface[] | null;
  systemPositions: PositionAPI[] | null;
  locations: RoomLocationInterface[];
  getDistrictPlacement: Function;
  getSchools: Function;
  getPositions: Function;
  getAllRoom: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface PlacementReportState {
  loading: boolean;
  loadingData: boolean;
  selectedStatus: PlacedCandidateStatus | "all";
  selectAll: boolean;
  // reportMainData: centerReportInterface[] | null;
  selectedDistrict: DistrictInterface | null;
  reportContentExport: placementReportContentInterface[];
  reportTitle: string;
  dataToPrint: PlacementToPrintInterface[] | null;
  sortProperty: string;
  isSorted: boolean;
  sortOrder: "ASC" | "DESC";
  searchInput: string;
  searchDistrict: string;
  generateReportModal: {
    reportType: "EXCEL" | "PDF";
    status: boolean;
  } | null;
  formError: {
    element: string;
    msg: string;
  } | null;
  // selectedDistrict: roomDistrictInterface | "all";
  chooseDistrict: boolean;
  downloading: boolean;
}

export class _PlacementReport extends Component<
  PlacementReportProps,
  PlacementReportState
> {
  constructor(props: PlacementReportProps) {
    super(props);

    this.state = {
      loading: true,
      loadingData: true,
      selectedStatus: "all",
      selectAll: false,
      selectedDistrict: null,
      // selectedReportTitle: centerReportTitleType;
      // reportMainData: centerReportInterface[] | null;
      reportContentExport: defaultReportContent,
      reportTitle: "",
      dataToPrint: null,
      sortProperty: "",
      isSorted: false,
      sortOrder: "ASC",
      searchInput: "",
      searchDistrict: "",
      generateReportModal: null,
      formError: null,
      // selectedDistrict: roomDistrictInterface | "all";
      chooseDistrict: true,
      downloading: false,
    };
  }

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };

  setLoadingData = (status: boolean) => {
    this.setState({ loadingData: status });
  };

  setSelectAll = (status: boolean) => {
    this.setState({ selectAll: status });
  };

  setSelectedDistrict = (value: DistrictInterface | null) => {
    this.setState({ selectedDistrict: value });
  };

  setReportTitle = (thisTitle: string) => {
    this.setState({ reportTitle: thisTitle });
  };

  setFormError = (
    data: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: data });
  };

  setSortOrder = (thisOrder: "ASC" | "DESC") => {
    this.setState({ sortOrder: thisOrder });
  };

  setSortProperty = (thisProperty: string) => {
    this.setState({ sortProperty: thisProperty });
  };

  setIsSorted = (status: boolean) => {
    this.setState({ isSorted: status });
  };

  setGenerateReportModal = (
    data: {
      reportType: "EXCEL" | "PDF";
      status: boolean;
    } | null
  ) => {
    this.setState({ generateReportModal: data });
  };

  setDownloading = (value: boolean) => {
    this.setState({ downloading: value });
  };

  distinctDistrict = (
    location: RoomLocationInterface[],
    province_id?: string
  ): DistrictInterface[] => {
    let ids: string[] = [],
      result: DistrictInterface[] = [];

    if (province_id) {
      result = location.find((item) => item._id === province_id)
        ? location.find((item) => item._id === province_id)!.districts
          ? location.find((item) => item._id === province_id)!.districts
          : []
        : [];
    } else {
      for (const key in location) {
        if (Object.prototype.hasOwnProperty.call(location, key)) {
          for (const keyDistrict in location[key].districts) {
            if (
              Object.prototype.hasOwnProperty.call(
                location[key].districts,
                keyDistrict
              )
            ) {
              if (!ids.includes(location[key].districts[keyDistrict]._id)) {
                ids.push(location[key].districts[keyDistrict]._id);
                result.push(location[key].districts[keyDistrict]);
              }
            }
          }
        }
      }
    }

    return result;
  };

  setSort = (thisProperty: string) => {
    if (
      this.state.sortProperty === thisProperty &&
      this.state.sortOrder === "ASC"
    ) {
      this.setSortOrder("DESC");
    } else {
      this.setSortOrder("ASC");
    }
    this.setSortProperty(thisProperty);
    this.setIsSorted(true);
  };

  loadPlacementData = (thisDistrict: DistrictInterface) => {
    this.props.getDistrictPlacement(
      thisDistrict._id,
      (status: boolean, errorMsg: string) => {
        this.setLoadingData(status);
        if (!status && !isEmptyOrSpaces(errorMsg)) {
          this.props.PushPopupAlert({
            title: errorMsg,
            theme: PopupAlertTheme.warning,
          });
        }
      }
    );
  };

  componentDidMount = () => {
    if (this.props.schools.length === 0 && this.state.loading) {
      this.props.getSchools(
        (status: boolean, errorMsg: string, data?: RoomLocationInterface[]) => {
          this.setLoading(status);
          if (!status && !isEmptyOrSpaces(errorMsg)) {
            this.props.PushPopupAlert({
              title: errorMsg,
              theme: PopupAlertTheme.warning,
            });
          }
          if (data && !this.props.districtPlacements) {
            this.setState({ chooseDistrict: true });
          }
        }
      );
    } else if (!this.props.districtPlacements) {
      this.setState({ chooseDistrict: true });
    }

    if (this.props.locations.length === 0) {
      this.props.getAllRoom(
        (status: boolean, msg: string, data?: RoomLocationInterface[]) => {
          this.setLoading(status);
          if (!status && msg.length > 0) {
            console.log(msg);
          }
        }
      );
    } else {
      this.setLoading(false);
    }
  };

  FC_GeneratePDF = () => {
    if (!document.querySelectorAll("#report_content tbody tr").length) {
      this.props.PushPopupAlert({
        title: `Can't generate Report of empty Data`,
        theme: PopupAlertTheme.warning,
      });
      return false;
    }

    if (this.state.selectedDistrict) {
      const table: HTMLElement | null =
        document.getElementById("report_content");
      const reportSettings: reportSettingsInterface = {
        doneBy: this.props.auth.user.fname + " " + this.props.auth.user.lname,
        title: "Placement Report",
        subjectTitle: `${this.state.selectedDistrict.district_name} district`,
        subject: "All room",
        period: "All session",
        footer: "Online Recruitment",
        institute: "All institute",
      };
      table && generatePDF(reportSettings, table, this.setDownloading);
    }
  };

  updateContentToPrint = () => {
    // UPDATE REPORT TITLE ========================
    if (this.state.selectedDistrict) {
      let title =
        "Room Report - " +
        this.state.selectedDistrict.district_name +
        " district";
      // if (this.state.selectedSession) {
      //   title =
      //     "Room Report " +
      //     this.state.selectedDistrict.district_name +
      //     " district - " +
      //     this.state.selectedSession.session_plan_name;
      // }
      this.setReportTitle(title);
    }
    // UPDATE CONTENT =============================
    if (this.props.districtPlacements) {
      let arrayNew: PlacementToPrintInterface[] = [];
      let n: number = 1;
      for (const key in this.props.districtPlacements) {
        if (
          Object.prototype.hasOwnProperty.call(
            this.props.districtPlacements,
            key
          )
        ) {
          let thisSchool = this.props.schools.find(
            (item) => item._id === this.props.districtPlacements![key].school_id
          );
          let thisPosition = this.props.systemPositions
            ? this.props.systemPositions.find(
                (item) =>
                  item.position_id ===
                  this.props.districtPlacements![key].position_id
              )
            : undefined;
          let dataArray: PlacementToPrintInterface | null = null;
          dataArray = {
            no: n,
            names:
              this.props.districtPlacements![key].user_info.fname +
              " " +
              this.props.districtPlacements![key].user_info.lname,
            national_id: this.props.districtPlacements![key].n_id,
            phone: this.props.districtPlacements![key].phone_number,
            email: this.props.districtPlacements![key].email,
            school_name: thisSchool ? thisSchool.school_name : "",
            position_name: thisPosition ? thisPosition.position_title : "",
            status: this.props.districtPlacements![key].status,
          };

          for (const key in this.state.reportContentExport) {
            if (
              Object.prototype.hasOwnProperty.call(
                this.state.reportContentExport,
                key
              )
            ) {
              if (!this.state.reportContentExport[key].status && dataArray) {
                delete dataArray[this.state.reportContentExport[key].key];
              }
            }
          }
          dataArray && arrayNew.push(dataArray);
          n++;
        }
      }
      this.setState({ dataToPrint: arrayNew });
      arrayNew = [];
      n = 1;
    }
  };

  updateReportContent = (key: placementReportContentKeyInterface) => {
    let index: number = this.state.reportContentExport.findIndex(
      (item) => item.key === key
    );
    let newArray: placementReportContentInterface[] =
      this.state.reportContentExport;
    newArray[index].status = !newArray[index].status;
    this.setState({ reportContentExport: newArray });
    let isSelectAllNeeded = newArray.find((item) => item.status === false);
    if (isSelectAllNeeded) {
      this.setSelectAll(false);
    } else {
      this.setSelectAll(true);
    }

    // UPDATE APPLICATION LIST FOR UPDATE ==============================
    this.updateContentToPrint();
  };

  isChangeAllowed = (key: placementReportContentKeyInterface): boolean => {
    if (
      key === placementReportContentKeyInterface.applicant_name ||
      key === placementReportContentKeyInterface.position_name ||
      key === placementReportContentKeyInterface.national_id
    ) {
      return false;
    }
    return true;
  };

  resetReportContent = (currentState: boolean) => {
    let newArray: placementReportContentInterface[] =
      this.state.reportContentExport;
    for (const key in newArray) {
      if (this.isChangeAllowed(newArray[key].key)) {
        newArray[key].status = !currentState;
      }
    }

    this.setState({ reportContentExport: newArray });
    this.setSelectAll(!currentState);
    this.updateContentToPrint();
  };

  dataToShow = (
    placements: placedCandidateInterface[]
  ): PlacementToPrintInterface[] => {
    let result: PlacementToPrintInterface[] = [];
    for (const key in placements) {
      if (Object.prototype.hasOwnProperty.call(placements, key)) {
        // const element = placements[key];
        let thisSchool = this.props.schools.find(
          (item) => item._id === this.props.districtPlacements![key].school_id
        );
        let thisPosition = this.props.systemPositions
          ? this.props.systemPositions.find(
              (item) =>
                item.position_id ===
                this.props.districtPlacements![key].position_id
            )
          : undefined;
        result.push({
          no: Math.random(),
          names:
            this.props.districtPlacements![key].user_info.fname +
            " " +
            this.props.districtPlacements![key].user_info.lname,
          national_id: this.props.districtPlacements![key].n_id,
          phone: this.props.districtPlacements![key].phone_number,
          email: this.props.districtPlacements![key].email,
          school_name: thisSchool ? thisSchool.school_name : "",
          position_name: thisPosition ? thisPosition.position_title : "",
          status: this.props.districtPlacements![key].status,
        });
      }
    }
    return result;
  };

  FC_FilterReportData = (
    allData: PlacementToPrintInterface[],
    status: PlacedCandidateStatus | "all"
  ): PlacementToPrintInterface[] => {
    let result: PlacementToPrintInterface[] = allData;
    if (status !== "all") {
      result = result.filter((item) => item.status === status);
    }

    return result;
  };

  PlacementStatuses = (
    allData: placedCandidateInterface[]
  ): {
    title: string;
    count: number;
    status: PlacedCandidateStatus | "all";
  }[] => {
    let output: {
      title: string;
      count: number;
      status: PlacedCandidateStatus | "all";
    }[] = [{ title: "All", count: allData.length, status: "all" }];
    output.push(
      {
        title: capitalizeFirstLetter(
          PlacedCandidateStatus.PENDING.toLocaleLowerCase()
        ),
        count: allData.filter(
          (itm) => itm.status === PlacedCandidateStatus.PENDING
        ).length,
        status: PlacedCandidateStatus.PENDING,
      },
      {
        title: capitalizeFirstLetter(
          PlacedCandidateStatus.PLACED.toLocaleLowerCase()
        ),
        count: allData.filter(
          (itm) => itm.status === PlacedCandidateStatus.PLACED
        ).length,
        status: PlacedCandidateStatus.PLACED,
      },
      {
        title: capitalizeFirstLetter(
          PlacedCandidateStatus.REJECTED.toLocaleLowerCase()
        ),
        count: allData.filter(
          (itm) => itm.status === PlacedCandidateStatus.REJECTED
        ).length,
        status: PlacedCandidateStatus.REJECTED,
      }
    );
    return output;
  };

  render() {
    if (this.state.loading) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading data...
        </div>
      );
    }

    return (
      <div>
        <SideNavigation>
          <div className="">
            <div className="text-gray-600 text-sm mb-2">District:</div>
            <div
              onClick={() => this.setState({ chooseDistrict: true })}
              className="font-bold bg-blue-50 border border-blue-200 rounded py-2 cursor-pointer text-xs px-2 hover:underline justify-between text-blue-600  mb-4 w-full flex flex-row items-center animate__animated animate__zoomIn"
            >
              {this.state.selectedDistrict !== null
                ? `${this.state.selectedDistrict.district_name}`
                : "Choose district"}
              <FiEdit className="text-lg text-blue-600" />
            </div>
          </div>

          <div className="pt-1">
            <div className="text-sm text-gray-700 pb-2 pt-2">
              Placement statuses:
            </div>
            {this.props.districtPlacements === null ||
            this.state.loadingData ? (
              <Loading />
            ) : (
              this.PlacementStatuses(this.props.districtPlacements!).map(
                (
                  itm: {
                    title: string;
                    count: number;
                    status: PlacedCandidateStatus | "all";
                  },
                  i: number
                ) => (
                  // itm.count > 0 &&
                  <Fragment key={itm.title}>
                    <div
                      onClick={() =>
                        this.setState({ selectedStatus: itm.status })
                      }
                    >
                      <MainItem
                        title={itm.title}
                        count={itm.count}
                        active={
                          this.state.selectedStatus === itm.status
                            ? true
                            : false
                        }
                      />
                    </div>
                  </Fragment>
                )
              )
            )}
          </div>
        </SideNavigation>

        {/* BODY CONTENT--------------------------------- */}
        <section>
          {/* REPORT TITLE ============================================ */}
          <div className="grid md:grid-cols-12">
            <div className="col-span-8">
              <div className="mt-4 mx-4 flex flex-col gap-1">
                <span className="text-3xl font-bold text-gray-700">
                  Placement report
                </span>
              </div>
            </div>
            <div className="col-span-4">
              <div className="float-right mt-2">
                {this.props.districtPlacements &&
                  this.props.districtPlacements.length > 0 && (
                    <div className="flex flex-row items-center justify-end font-bold mb-2">
                      <BiExport className="text-2xl text-gray-500" />
                      <span className="text-gray-500">Export as</span>
                      <button
                        className="px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200"
                        disabled={
                          this.props.districtPlacements &&
                          !this.state.downloading
                            ? false
                            : true
                        }
                        onClick={() => {
                          // this.updateContentToPrint();
                          this.setGenerateReportModal({
                            reportType: "EXCEL",
                            status: true,
                          });
                        }}
                      >
                        EXCEL
                      </button>
                      <button
                        className={`px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200 ${
                          this.state.downloading && "cursor-wait"
                        }`}
                        onClick={() => this.FC_GeneratePDF()}
                        disabled={this.props.districtPlacements ? false : true}
                      >
                        PDF
                      </button>
                    </div>
                  )}
              </div>
            </div>
          </div>
          {/* END REPORT TITLE ============================================ */}

          {/* ######################################################## */}
          <div className="bg-white my-4">
            <div className="w-full px-2 py-2">
              <input
                onChange={(e) => this.setState({ searchInput: e.target.value })}
                type="text"
                value={this.state.searchInput}
                placeholder="Search..."
                className="w-full bg-gray-100 py-2 px-3 rounded focus:border-gray-800"
                disabled={
                  this.state.loadingData === null ||
                  this.props.districtPlacements === null ||
                  this.FC_FilterReportData(
                    this.dataToShow(this.props.districtPlacements),
                    this.state.selectedStatus
                  ).length <= 0
                }
              />
            </div>
            <table className="w-full shadow" id="report_content">
              <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                <tr>
                  <td className="px-2 py-2">#</td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("names")}
                  >
                    <div className="flex items-center gap-1">
                      Names <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("national_id")}
                  >
                    <div className="flex items-center gap-1">
                      National Id <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("phone")}
                  >
                    <div className="flex items-center gap-1">
                      Phone <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("email")}
                  >
                    <div className="flex items-center gap-1">
                      Email <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("position_name")}
                  >
                    <div className="flex items-center gap-1">
                      Position <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("school_name")}
                  >
                    <div className="flex items-center gap-1">
                      School <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("status")}
                  >
                    <div className="flex items-center gap-1">
                      Status <BiSort />
                    </div>
                  </td>
                </tr>
              </thead>
              <tbody>
                {this.props.districtPlacements === null ||
                this.state.loadingData === null ? (
                  <TableLoading cols={8} />
                ) : this.FC_FilterReportData(
                    this.dataToShow(this.props.districtPlacements),
                    this.state.selectedStatus
                  ).length === 0 ? (
                  <tr>
                    <td colSpan={8} className="px-2 py-2">
                      <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                        <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                        <span className="text-sm font-bold">
                          No result found! Please try to search again
                        </span>
                        <div className="w-full mt-2 flex flex-row items-center justify-center">
                          <ul className="text-sm py-2 text-blue-600 font-bold flex flex-row gap-2">
                            <li
                              onClick={() =>
                                this.setState({ chooseDistrict: true })
                              }
                              className="underline text-gray-500 cursor-pointer"
                            >
                              Choose another district
                            </li>
                          </ul>
                        </div>
                      </div>
                    </td>
                  </tr>
                ) : (
                  sortThisArray(
                    this.state.isSorted,
                    search(
                      this.FC_FilterReportData(
                        this.dataToShow(this.props.districtPlacements),
                        this.state.selectedStatus
                      ),
                      this.state.searchInput
                    ),
                    this.state.sortProperty,
                    this.state.sortOrder
                  ).map((item: PlacementToPrintInterface, i) => (
                    <tr
                      key={i + item.no}
                      className={`${
                        i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                      } hover:bg-blue-100 hover:text-blue-700 border-b hover:border-blue-500 text-sm ${
                        item.status === PlacedCandidateStatus.REJECTED
                          ? "border-yellow-500 text-gray-700 bg-yellow-200"
                          : item.status === PlacedCandidateStatus.PENDING
                          ? "border-gray-500 text-gray-700 bg-gray-200"
                          : "text-gray-700 border-blue-300 bg-blue-200"
                      }`}
                    >
                      <td className="px-2 py-2">{i + 1}</td>
                      <td className="px-2 py-2">{item.names}</td>
                      <td className="px-2 py-2">{item.national_id}</td>
                      <td className="px-2 py-2">{item.phone}</td>
                      <td className="px-2 py-2">{item.email}</td>
                      <td className="px-2 py-2">{item.position_name}</td>
                      <td className="px-2 py-2">{item.school_name}</td>
                      <td className="px-2 py-2">{item.status}</td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        </section>
        {/* END BODY CONTENT--------------------------------- */}

        {/* ---------------------------------------------------------------------------- */}
        {/* PERIOD && POSITION CONTENT MODAL */}
        {this.state.chooseDistrict === true && (
          <Modal
            title={
              <div className="flex flex-row gap-4 items-center">
                <IoFilter className="text-5xl text-blue-600" />
                <div className="flex flex-col">
                  <span className="font-bold text-xl text-blue-600">
                    Choose a district
                  </span>
                </div>
              </div>
            }
            backDrop={true}
            theme={Themes.default}
            close={() =>
              this.state.selectedDistrict !== null &&
              this.setState({ chooseDistrict: false })
            }
            backDropClose={true}
            widthSizeClass={ModalSize.medium}
            displayClose={true}
            padding={{ title: true }}
          >
            <div className="px-2 pb-2">
              {this.distinctDistrict(this.props.locations).length === 0 ? (
                <div className="flex flex-col text-gray-400 items-center justify-center">
                  <MdSearch className="text-6xl" />
                  <div className="flex flex-col items-center justify-center">
                    <span>No district found! Please go back!</span>
                    <Link
                      to="/"
                      className="btn bg-yellow-100 text-yellow-700 my-4 hover:bg-yellow-200"
                    >
                      Go back
                    </Link>
                  </div>
                </div>
              ) : (
                <>
                  <div className="flex flex-col">
                    <span className="text-gray-600 text-xs font-bold">
                      Filter by district name
                    </span>
                    <input
                      onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                        this.setState({ searchDistrict: e.target.value })
                      }
                      type="text"
                      className="mb-4 bg-gray-100 px-3 py-2 rounded"
                      placeholder="Keyword..."
                    />
                  </div>
                  <div className=" max-h-96 overflow-y-auto">
                    {searchData(
                      this.distinctDistrict(this.props.locations),
                      this.state.searchDistrict
                    ).map((item: DistrictInterface, key) => (
                      <div
                        key={key + 1}
                        onClick={() => {
                          this.setSelectedDistrict(item);
                          this.loadPlacementData(item);
                          this.setState({
                            chooseDistrict: false,
                            reportTitle:
                              "Placement report in " +
                              item.district_name +
                              " district",
                          });
                        }}
                        className="bg-white cursor-pointer rounded  px-3 py-2 border border-gray-200 hover:border-blue-500 hover:bg-blue-100 mb-2"
                      >
                        <div className="grid lg:grid-cols-12">
                          <div className="col-span-12 lg:col-span-10">
                            <div className="flex flex-row items-center gap-3">
                              <div>
                                <MdLocationOn className="text-2xl md:text-4xl text-blue-600" />
                              </div>
                              <div className="flex flex-col">
                                <span className="text-xs lg:text-sm font-bold text-gray-700">
                                  {item.district_name}
                                </span>
                              </div>
                            </div>
                          </div>
                          <div className="col-span-12 lg:col-span-2 hidden lg:block">
                            <div className="h-full flex items-center justify-end">
                              <div className="btn bg-blue-600 text-white float-right">
                                Choose
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                </>
              )}
            </div>
          </Modal>
        )}
        {/* END PERIOD && POSITION CONTENT MODAL */}

        {/* --------------------------------------------------------------- */}
        {/* GENERATE REPORT MODAL CONTENT MODAL */}
        {this.state.generateReportModal !== null &&
          this.state.generateReportModal.status === true && (
            <Modal
              title={
                <div className="flex flex-row gap-4 items-center">
                  {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
                  <div className="flex flex-col">
                    <span className="font-bold text-2xl text-gray-500">
                      Generating Report contents
                    </span>
                    <span className="text-sm text-blue-600 font-normal">
                      Generating {this.state.generateReportModal.reportType}{" "}
                      Report
                    </span>
                  </div>
                </div>
              }
              backDrop={true}
              theme={Themes.default}
              close={() => this.setGenerateReportModal(null)}
              backDropClose={false}
              widthSizeClass={ModalSize.medium}
              displayClose={true}
              padding={{ title: true }}
            >
              <div className="px-6">
                <div className="my-2 text-base text-gray-700 sm:text-lg">
                  <div className="flex flex-col">
                    <label>Title</label>
                    <input
                      type="text"
                      className={`text-left input-white w-20 inline-block ${
                        this.state.formError &&
                        this.state.formError.element === "report_title"
                          ? "border-red-500"
                          : "border-gray-500"
                      } border-2`}
                      placeholder="Sub Category Title"
                      value={this.state.reportTitle}
                      onChange={(e) => {
                        this.setFormError(null);
                        this.setReportTitle(e.target.value);
                      }}
                      disabled={this.props.districtPlacements ? false : true}
                    />
                    {this.state.formError &&
                      this.state.formError.element === "report_title" && (
                        <span className="text-red-500">
                          {this.state.formError.msg}
                        </span>
                      )}
                  </div>
                  <div className="flex flex-col mt-2">
                    <label>Report content</label>
                    <div className="grid grid-cols-2 md:grid-cols-3 items-center gap-2 mt-2">
                      {this.state.reportContentExport.map((item, index) => (
                        <div
                          key={index}
                          className={`flex items-center gap-1 ${
                            this.isChangeAllowed(item.key)
                              ? "cursor-pointer"
                              : "cursor-not-allowed"
                          }`}
                          onClick={() =>
                            this.isChangeAllowed(item.key) &&
                            this.updateReportContent(item.key)
                          }
                          title={
                            !this.isChangeAllowed(item.key)
                              ? "Required for every report!"
                              : ""
                          }
                        >
                          {item.status ? (
                            <MdCheckBox className="w-6 h-6 text-blue-700" />
                          ) : (
                            <MdCheckBoxOutlineBlank className="w-6 h-6" />
                          )}
                          <span>{item.title}</span>
                        </div>
                      ))}
                    </div>
                    {this.state.formError &&
                      this.state.formError.element === "report_content" && (
                        <span className="text-red-500">
                          {this.state.formError.msg}
                        </span>
                      )}
                  </div>
                </div>
                <div className="flex justify-between mb-2 mt-4">
                  <div
                    className={`flex items-center gap-1 cursor-pointer`}
                    onClick={() =>
                      this.resetReportContent(this.state.selectAll)
                    }
                  >
                    {this.state.selectAll ? (
                      <MdCheckBox className="w-6 h-6 text-blue-700" />
                    ) : (
                      <MdCheckBoxOutlineBlank className="w-6 h-6" />
                    )}
                    <span>Select all</span>
                  </div>

                  {this.state.dataToPrint && this.props.districtPlacements && (
                    <ExportToExcel
                      fileData={this.state.dataToPrint}
                      fileName={this.state.reportTitle}
                      btnName="Generate"
                    />
                  )}
                </div>
              </div>
            </Modal>
          )}
        {/* END GENERATE REPORT MODAL CONTENT MODAL */}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  system_settings,
  schools,
  locations,
}: StoreState): {
  auth: Auth;
  schools: SchoolsInterface[];
  districtPlacements: placedCandidateInterface[] | null;
  locations: RoomLocationInterface[];
  systemPositions: PositionAPI[] | null;
} => {
  return {
    auth: auth,
    systemPositions: system_settings.systemPositions,
    schools: schools.schools,
    districtPlacements: schools.districtPlacements,
    locations: locations.roomLocation,
  };
};

export const PlacementReport = connect(mapStateToProps, {
  getDistrictPlacement,
  getAllRoom,
  getSchools,
  getPositions,
  PushPopupAlert,
})(_PlacementReport);
